#containerD {
  width: 100%;
  height: 700px;
  -webkit-perspective: 800px;
  -webkit-perspective-origin: 50% 225px;
  -moz-perspective: 800px;
  -moz-perspective-origin: 50% 225px;
  perspective: 800px;
  perspective-origin: 50% 225px;
}

#stageD {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

#shapeD {
  position: relative;
  top: 75%;
  margin: 0 auto;
  height: 200px;
  width: 200px;
  -webkit-transform: translateZ(-0px);
  -webkit-transform-style: preserve-3d;
  -moz-transform: translateZ(-0px);
  -moz-transform-style: preserve-3d;
  transform: translateZ(-0px);
  transform-style: preserve-3d;
}
.backfaces{
  border: none !important;
  box-shadow: none !important;
}
.ringD {
  position: absolute;
  display: block;
  height: 350px;
  width: 200px;
  text-align: center;
  font-family: Times, serif;
  font-size: 124pt;
  color: black;
  background-color: #fff;
  border-radius: 12%;
  border:1px solid white;
   box-shadow:
  0 0 30px 10px #ffeefc;
}

#shapeD {
  border: 0px;
  background-color: rgba(255, 255, 255, 0);
}

.ringD > .r1 {
  -webkit-transform: rotateY(296deg) translateZ(-380px);
  -moz-transform: rotateY(296deg) translateZ(-380px);
  transform: rotateY(296deg) translateZ(-380px);
}

.ringD > .r2 {
  -webkit-transform: rotateY(328deg) translateZ(-380px);
  -moz-transform: rotateY(328deg) translateZ(-380px);
  transform: rotateY(328deg) translateZ(-380px);
}

.ringD > .r3 {
  -webkit-transform: rotateY(0deg) translateZ(-380px);
  -moz-transform: rotateY(0deg) translateZ(-380px);
  transform: rotateY(0deg) translateZ(-380px);
}

.ringD > .r4 {
  -webkit-transform: rotateY(32deg) translateZ(-380px);
  -moz-transform: rotateY(32deg) translateZ(-380px);
  transform: rotateY(32deg) translateZ(-380px);
}

.ringD > .r5 {
  -webkit-transform: rotateY(64deg) translateZ(-380px);
  -moz-transform: rotateY(64deg) translateZ(-380px);
  transform: rotateY(64deg) translateZ(-380px);
}


.qMark{
