.drdLogo{
  width: 100%;
  opacity: 0;
  
}
.drdLogoAnimation{
  width: 100%;
  opacity: 0;
  -webkit-animation: logoFade 1s ease-out;
  -moz-animation: logoFade 1s ease-out;
  -o-animation: logoFade 1s ease-out;
  animation: logoFade 1s ease-out;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}
.logoContainer{
  background: black;
  opacity: 1;
}

.logoAnimation{
  opacity: 1;
  pointer-events:none;
  -webkit-animation: fadeInFromNone 12s ease-out;
  -moz-animation: fadeInFromNone 12s ease-out;
  -o-animation: fadeInFromNone 12s ease-out;
  animation: fadeInFromNone 12s ease-out;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}

@keyframes logoFade {
    0% {
        display: inherit;
        opacity: 0;
    }

    100% {
        display: inherit;
        opacity: 1;
        
    }
}

@keyframes fadeInFromNone {
    0% {
        display: inherit;
        opacity: 1;
        pointer-events:inherit;
    }

    5%{
      pointer-events:none;
    }

    99% {
        display: inherit;
        opacity: 0.01;
    }

    100% {
        display: none;
        opacity: 0;
    }
}

