.display{

  display: inherit;
  opacity: 0;
   -webkit-animation: fadeInFromNoneModal 1s ease-out;
  -moz-animation: fadeInFromNoneModal 1s ease-out;
  -o-animation: fadeInFromNoneModal 1s ease-out;
  animation: fadeInFromNoneModal 1s ease-out;
  animation-delay: .1s;
  animation-fill-mode: forwards;
}
