}

@keyframes fadeInFromNoneModal {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: inherit;
        opacity: 0;
    }

    100% {
        display: inherit;
        opacity: 1;
    }
}

.input{
  width:42%;
  height: 72px;
  border:0px;
  border-bottom: 1px solid white;
  color:white;
  background: transparent;
  font-size: 40px;
  font-family: "Traulha WEB Regulara";
  
   
}

.inputButton{
  -webkit-transition: all 0.30s ease-in-out;
  -moz-transition: all 0.30s ease-in-out;
  -ms-transition: all 0.30s ease-in-out;
  -o-transition: all 0.30s ease-in-out;
  outline: none;
  border:0px;
  border-bottom: 1px solid white;
  margin: 7px;
  padding: 7px;
  background: transparent;
  color:white;
  font-size: 32px;
  font-family: "Traulha WEB Regulara";
}

.inputButton:hover{
  background: radial-gradient(rgba(145, 152, 229, 1) 0%, rgba(145, 152, 229, 0) 70%);
}
input:focus { 

  background: radial-gradient(farthest-corner at 0px 36px, rgba(145, 152, 229, 1) 0%, rgba(145, 152, 229, 0) 70%);
  outline: none;

}
input:active { 
  background: radial-gradient(farthest-corner at 0px 36px, rgba(145, 152, 229, 1) 0%, rgba(145, 152, 229, 0) 70%);
  outline: none;

}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-text-fill-color: white;
    -webkit-transition-delay: 9999s;
    -webkit-transition: color 9999s ease-out, background-color 9999s ease-out;
}
.input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}
